<template>
  <div v-loading="loading" class="orangehrm-immigration">
    <div class="section-header">
      <h3 class="orangehrm-section-title">Thng tin xut nhp cnh</h3>
      <el-button type="primary" :icon="Plus" size="small" @click="handleAdd">
        Thm giy t
      </el-button>
    </div>

    <el-table
      v-if="documents.length > 0"
      :data="documents"
      border
      class="orangehrm-table"
    >
      <el-table-column
        prop="loai_giay_to"
        label="Loi giy t"
        min-width="150"
      />
      <el-table-column prop="so_giay_to" label="S giy t" min-width="150" />
      <el-table-column label="Ngy cp" width="120">
        <template #default="scope">
          {{ formatDate(scope.row.ngay_cap) }}
        </template>
      </el-table-column>
      <el-table-column label="Ngy ht hn" width="120">
        <template #default="scope">
          {{ formatDate(scope.row.ngay_het_han) }}
        </template>
      </el-table-column>
      <el-table-column prop="quoc_gia_cap" label="Quc gia cp" width="130" />
      <el-table-column label="Trng thi" width="130">
        <template #default="scope">
          <el-tag :type="getStatusType(scope.row.trang_thai)">{{
            scope.row.trang_thai
          }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="Thao tc" width="150" align="center">
        <template #default="scope">
          <el-button
            size="small"
            :icon="Edit"
            @click="handleEdit(scope.$index)"
          >
            Sa
          </el-button>
          <el-button
            size="small"
            type="danger"
            :icon="Delete"
            @click="handleDelete(scope.$index)"
          >
            Xa
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-empty
      v-else
      description="Cha c thng tin xut nhp cnh"
      :image-size="100"
    />

    <!-- Dialog -->
    <el-dialog
      v-model="dialogVisible"
      :title="editIndex === -1 ? 'Thm giy t' : 'Chnh sa giy t'"
      width="650px"
    >
      <el-form
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="150px"
      >
        <el-form-item label="Loi giy t" prop="loai_giay_to" required>
          <el-select
            v-model="form.loai_giay_to"
            placeholder="Chn loi giy t"
            style="width: 100%"
          >
            <el-option label="H chiu" value="H chiu" />
            <el-option label="Visa" value="Visa" />
            <el-option label="Th xanh (Green Card)" value="Th xanh" />
            <el-option label="Giy php lao ng" value="Giy php lao ng" />
            <el-option label="Giy php c tr" value="Giy php c tr" />
            <el-option label="Khc" value="Khc" />
          </el-select>
        </el-form-item>

        <el-form-item label="S giy t" prop="so_giay_to" required>
          <el-input
            v-model="form.so_giay_to"
            placeholder="S h chiu/visa/giy php"
          />
        </el-form-item>

        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="Ngy cp" prop="ngay_cap">
              <el-date-picker
                v-model="form.ngay_cap"
                type="date"
                placeholder="Chn ngy cp"
                format="DD/MM/YYYY"
                value-format="YYYY-MM-DD"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Ngy ht hn" prop="ngay_het_han">
              <el-date-picker
                v-model="form.ngay_het_han"
                type="date"
                placeholder="Chn ngy ht hn"
                format="DD/MM/YYYY"
                value-format="YYYY-MM-DD"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="Quc gia cp" prop="quoc_gia_cap">
          <el-input
            v-model="form.quoc_gia_cap"
            placeholder="V d: Vit Nam, USA, Singapore"
          />
        </el-form-item>

        <el-form-item label="Trng thi" prop="trang_thai" required>
          <el-select
            v-model="form.trang_thai"
            placeholder="Chn trng thi"
            style="width: 100%"
          >
            <el-option label="Cn hiu lc" value="Cn hiu lc" />
            <el-option label="Ht hn" value="Ht hn" />
            <el-option label="ang x l" value="ang x l" />
          </el-select>
        </el-form-item>

        <el-form-item label="Ghi ch" prop="ghi_chu">
          <el-input
            v-model="form.ghi_chu"
            type="textarea"
            :rows="3"
            placeholder="Ghi ch thm"
          />
        </el-form-item>
      </el-form>

      <template #footer>
        <el-button @click="dialogVisible = false">Hy</el-button>
        <el-button type="primary" :loading="saving" @click="handleSave">
          {{ editIndex === -1 ? 'Thm' : 'Cp nht' }}
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import {ref, reactive, watch} from 'vue';
import {Plus, Edit, Delete} from '@element-plus/icons-vue';
import {ElMessage, ElMessageBox, FormInstance, FormRules} from 'element-plus';
import nhanVienService from '@/services/nhanVienService';
import {NhanVien} from '@/types';
import dayjs from 'dayjs';

const props = defineProps<{
  employee: NhanVien | null;
  loading: boolean;
}>();

const emit = defineEmits<{
  (e: 'reload'): void;
}>();

const dialogVisible = ref(false);
const formRef = ref<FormInstance>();
const editIndex = ref(-1);
const saving = ref(false);
const documents = ref<any[]>([]);

const form = reactive({
  loai_giay_to: '',
  so_giay_to: '',
  ngay_cap: '',
  ngay_het_han: '',
  quoc_gia_cap: '',
  trang_thai: '',
  ghi_chu: '',
});

const formRules: FormRules = {
  loai_giay_to: [
    {required: true, message: 'Vui lng chn loi giy t', trigger: 'change'},
  ],
  so_giay_to: [
    {required: true, message: 'Vui lng nhp s giy t', trigger: 'blur'},
  ],
  trang_thai: [
    {required: true, message: 'Vui lng chn trng thi', trigger: 'change'},
  ],
};

watch(
  () => props.employee,
  (newEmployee) => {
    if (newEmployee) {
      documents.value = [...(newEmployee.xuat_nhap_canh || [])];
    } else {
      documents.value = [];
    }
  },
  {immediate: true, deep: true},
);

const handleAdd = () => {
  editIndex.value = -1;
  form.loai_giay_to = '';
  form.so_giay_to = '';
  form.ngay_cap = '';
  form.ngay_het_han = '';
  form.quoc_gia_cap = 'Vit Nam';
  form.trang_thai = '';
  form.ghi_chu = '';
  dialogVisible.value = true;
};

const handleEdit = (index: number) => {
  editIndex.value = index;
  const doc = documents.value[index];
  form.loai_giay_to = doc.loai_giay_to || '';
  form.so_giay_to = doc.so_giay_to || '';
  form.ngay_cap = doc.ngay_cap || '';
  form.ngay_het_han = doc.ngay_het_han || '';
  form.quoc_gia_cap = doc.quoc_gia_cap || '';
  form.trang_thai = doc.trang_thai || '';
  form.ghi_chu = doc.ghi_chu || '';
  dialogVisible.value = true;
};

const handleDelete = async (index: number) => {
  try {
    await ElMessageBox.confirm(
      'Bn c chc chn mun xa giy t ny?',
      'Xc nhn xa',
      {
        confirmButtonText: 'Xa',
        cancelButtonText: 'Hy',
        type: 'warning',
      },
    );

    const updatedDocs = [...documents.value];
    updatedDocs.splice(index, 1);

    saving.value = true;
    await nhanVienService.update(props.employee!._id, {
      xuat_nhap_canh: updatedDocs,
    });
    ElMessage.success('Xa giy t thnh cng');
    emit('reload');
  } catch (err: any) {
    if (err !== 'cancel') {
      ElMessage.error(err.response?.data?.msg || 'Khng th xa giy t');
    }
  } finally {
    saving.value = false;
  }
};

const handleSave = async () => {
  if (!formRef.value) return;
  if (!props.employee) return;

  try {
    const valid = await formRef.value.validate();
    if (!valid) return;

    saving.value = true;

    const updatedDocs = [...documents.value];

    if (editIndex.value === -1) {
      updatedDocs.push({...form});
    } else {
      updatedDocs[editIndex.value] = {...form};
    }

    await nhanVienService.update(props.employee._id, {
      xuat_nhap_canh: updatedDocs,
    });

    ElMessage.success(
      editIndex.value === -1
        ? 'Thm giy t thnh cng'
        : 'Cp nht giy t thnh cng',
    );
    dialogVisible.value = false;
    emit('reload');
  } catch (err: any) {
    ElMessage.error(err.response?.data?.msg || 'Khng th lu giy t');
  } finally {
    saving.value = false;
  }
};

const formatDate = (date: any) => {
  if (!date) return '';
  return dayjs(date).format('DD/MM/YYYY');
};

const getStatusType = (status: string) => {
  const typeMap: Record<string, string> = {
    'Cn hiu lc': 'success',
    'Ht hn': 'danger',
    'ang x l': 'warning',
  };
  return typeMap[status] || '';
};
</script>

<style lang="scss" scoped>
@import './employee-form-styles.scss';

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.orangehrm-section-title {
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  color: $text-primary;
  margin: 0;
}

.orangehrm-table {
  margin-top: 20px;
}
</style>
