const getSkillLevelType = (level: string) => {
  const typeMap: Record<string, string> = {
    'C bn': 'info',
    'Trung bnh': '',
    Kh: 'success',
    Gii: 'warning',
    'Chuyn gia': 'danger',
  };
  return typeMap[level] || '';
};

const formatDate = (date: any) => {
  if (!date) return '';
  return dayjs(date).format('DD/MM/YYYY');
};
