<template>
  <div v-loading="loading" class="orangehrm-emergency-contacts">
    <div class="section-header">
      <h3 class="orangehrm-section-title">Lin h khn cp</h3>
      <el-button type="primary" :icon="Plus" size="small" @click="handleAdd">
        Thm lin h
      </el-button>
    </div>

    <el-table
      v-if="contacts.length > 0"
      :data="contacts"
      border
      class="orangehrm-table"
    >
      <el-table-column prop="ten" label="H tn" min-width="180" />
      <el-table-column prop="moi_quan_he" label="Mi quan h" width="150">
        <template #default="scope">
          <el-tag>{{ scope.row.moi_quan_he }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="dien_thoai_nha" label="T nh" width="130" />
      <el-table-column prop="di_dong" label="Di ng" width="130" />
      <el-table-column
        prop="dien_thoai_cong_viec"
        label="T cng vic"
        width="130"
      />
      <el-table-column label="Thao tc" width="150" align="center">
        <template #default="scope">
          <el-button
            size="small"
            :icon="Edit"
            @click="handleEdit(scope.$index)"
          >
            Sa
          </el-button>
          <el-button
            size="small"
            type="danger"
            :icon="Delete"
            @click="handleDelete(scope.$index)"
          >
            Xa
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-empty
      v-else
      description="Cha c thng tin lin h khn cp"
      :image-size="100"
    />

    <!-- Dialog -->
    <el-dialog
      v-model="dialogVisible"
      :title="
        editIndex === -1
          ? 'Thm lin h khn cp'
          : 'Chnh sa lin h khn cp'
      "
      width="600px"
    >
      <el-form
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="140px"
      >
        <el-form-item label="H tn" prop="ten" required>
          <el-input v-model="form.ten" placeholder="H v tn ngi lin h" />
        </el-form-item>

        <el-form-item label="Mi quan h" prop="moi_quan_he" required>
          <el-select
            v-model="form.moi_quan_he"
            placeholder="Chn mi quan h"
            style="width: 100%"
          >
            <el-option label="Cha" value="Cha" />
            <el-option label="M" value="M" />
            <el-option label="V/Chng" value="V/Chng" />
            <el-option label="Con" value="Con" />
            <el-option label="Anh/Ch/Em" value="Anh/Ch/Em" />
            <el-option label="Bn" value="Bn" />
            <el-option label="Ngi thn" value="Ngi thn" />
            <el-option label="ng nghip" value="ng nghip" />
            <el-option label="Khc" value="Khc" />
          </el-select>
        </el-form-item>

        <el-form-item label="T nh" prop="dien_thoai_nha">
          <el-input
            v-model="form.dien_thoai_nha"
            placeholder="S in thoi nh"
          />
        </el-form-item>

        <el-form-item label="Di ng" prop="di_dong" required>
          <el-input
            v-model="form.di_dong"
            placeholder="S in thoi di ng"
          />
        </el-form-item>

        <el-form-item label="T cng vic" prop="dien_thoai_cong_viec">
          <el-input
            v-model="form.dien_thoai_cong_viec"
            placeholder="S in thoi cng vic"
          />
        </el-form-item>
      </el-form>

      <template #footer>
        <el-button @click="dialogVisible = false">Hy</el-button>
        <el-button type="primary" :loading="saving" @click="handleSave">
          {{ editIndex === -1 ? 'Thm' : 'Cp nht' }}
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import {ref, reactive, watch} from 'vue';
import {Plus, Edit, Delete} from '@element-plus/icons-vue';
import {ElMessage, ElMessageBox, FormInstance, FormRules} from 'element-plus';
import nhanVienService from '@/services/nhanVienService';
import {NhanVien} from '@/types';

const props = defineProps<{
  employee: NhanVien | null;
  loading: boolean;
}>();

const emit = defineEmits<{
  (e: 'reload'): void;
}>();

const dialogVisible = ref(false);
const formRef = ref<FormInstance>();
const editIndex = ref(-1);
const saving = ref(false);
const contacts = ref<any[]>([]);

const form = reactive({
  ten: '',
  moi_quan_he: '',
  dien_thoai_nha: '',
  di_dong: '',
  dien_thoai_cong_viec: '',
});

const formRules: FormRules = {
  ten: [{required: true, message: 'Vui lng nhp h tn', trigger: 'blur'}],
  moi_quan_he: [
    {required: true, message: 'Vui lng chn mi quan h', trigger: 'change'},
  ],
  di_dong: [
    {required: true, message: 'Vui lng nhp s di ng', trigger: 'blur'},
  ],
};

watch(
  () => props.employee,
  (newEmployee) => {
    if (newEmployee) {
      contacts.value = [...(newEmployee.lien_he_khan_cap || [])];
    } else {
      contacts.value = [];
    }
  },
  {immediate: true, deep: true},
);

const handleAdd = () => {
  editIndex.value = -1;
  form.ten = '';
  form.moi_quan_he = '';
  form.dien_thoai_nha = '';
  form.di_dong = '';
  form.dien_thoai_cong_viec = '';
  dialogVisible.value = true;
};

const handleEdit = (index: number) => {
  editIndex.value = index;
  const contact = contacts.value[index];
  form.ten = contact.ten || '';
  form.moi_quan_he = contact.moi_quan_he || '';
  form.dien_thoai_nha = contact.dien_thoai_nha || '';
  form.di_dong = contact.di_dong || '';
  form.dien_thoai_cong_viec = contact.dien_thoai_cong_viec || '';
  dialogVisible.value = true;
};

const handleDelete = async (index: number) => {
  try {
    await ElMessageBox.confirm(
      'Bn c chc chn mun xa thng tin lin h ny?',
      'Xc nhn xa',
      {
        confirmButtonText: 'Xa',
        cancelButtonText: 'Hy',
        type: 'warning',
      },
    );

    const updatedContacts = [...contacts.value];
    updatedContacts.splice(index, 1);

    saving.value = true;
    await nhanVienService.update(props.employee!._id, {
      lien_he_khan_cap: updatedContacts,
    });
    ElMessage.success('Xa lin h khn cp thnh cng');
    emit('reload');
  } catch (err: any) {
    if (err !== 'cancel') {
      ElMessage.error(
        err.response?.data?.msg || 'Khng th xa lin h khn cp',
      );
    }
  } finally {
    saving.value = false;
  }
};

const handleSave = async () => {
  if (!formRef.value) return;
  if (!props.employee) return;

  try {
    const valid = await formRef.value.validate();
    if (!valid) return;

    saving.value = true;

    const updatedContacts = [...contacts.value];

    if (editIndex.value === -1) {
      updatedContacts.push({...form});
    } else {
      updatedContacts[editIndex.value] = {...form};
    }

    await nhanVienService.update(props.employee._id, {
      lien_he_khan_cap: updatedContacts,
    });

    ElMessage.success(
      editIndex.value === -1
        ? 'Thm lin h khn cp thnh cng'
        : 'Cp nht lin h khn cp thnh cng',
    );
    dialogVisible.value = false;
    emit('reload');
  } catch (err: any) {
    ElMessage.error(
      err.response?.data?.msg || 'Khng th lu lin h khn cp',
    );
  } finally {
    saving.value = false;
  }
};
</script>

<style lang="scss" scoped>
@import './employee-form-styles.scss';

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.orangehrm-section-title {
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  color: $text-primary;
  margin: 0;
}

.orangehrm-table {
  margin-top: 20px;
}
</style>
