<template>
  <div v-loading="loading" class="orangehrm-experience">
    <div class="orangehrm-form-section">
      <div class="section-header">
        <h3 class="orangehrm-section-title">Kinh nghim lm vic</h3>
        <el-button
          v-if="!isEditing"
          type="primary"
          :icon="Plus"
          size="small"
          @click="handleAdd"
        >
          Thm kinh nghim
        </el-button>
      </div>

      <!-- Experience Table -->
      <el-table
        v-if="experiences.length > 0"
        :data="experiences"
        border
        class="orangehrm-table"
        style="margin-top: 20px"
      >
        <el-table-column prop="cong_ty" label="Cng ty" min-width="150" />
        <el-table-column prop="chuc_danh" label="Chc danh" min-width="120" />
        <el-table-column label="T ngy" min-width="110">
          <template #default="scope">
            {{ formatDate(scope.row.ngay_bat_dau) }}
          </template>
        </el-table-column>
        <el-table-column label="n ngy" min-width="110">
          <template #default="scope">
            {{ formatDate(scope.row.ngay_ket_thuc) }}
          </template>
        </el-table-column>
        <el-table-column
          prop="mo_ta"
          label="M t"
          min-width="200"
          show-overflow-tooltip
        />
        <el-table-column label="Thao tc" width="150" align="center">
          <template #default="scope">
            <el-button
              size="small"
              :icon="Edit"
              @click="handleEdit(scope.$index)"
            >
              Sa
            </el-button>
            <el-button
              size="small"
              type="danger"
              :icon="Delete"
              @click="handleDelete(scope.$index)"
            >
              Xa
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-empty
        v-else
        description="Cha c kinh nghim lm vic"
        :image-size="100"
      />
    </div>

    <!-- Add/Edit Dialog -->
    <el-dialog
      v-model="dialogVisible"
      :title="
        editIndex === -1
          ? 'Thm kinh nghim lm vic'
          : 'Chnh sa kinh nghim lm vic'
      "
      width="700px"
    >
      <el-form
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="140px"
      >
        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="Cng ty" prop="cong_ty" required>
              <el-input v-model="form.cong_ty" placeholder="Tn cng ty" />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Chc danh" prop="chuc_danh" required>
              <el-input
                v-model="form.chuc_danh"
                placeholder="V tr cng vic"
              />
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="T ngy" prop="ngay_bat_dau" required>
              <el-date-picker
                v-model="form.ngay_bat_dau"
                type="date"
                placeholder="Chn ngy"
                format="DD/MM/YYYY"
                value-format="YYYY-MM-DD"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="n ngy" prop="ngay_ket_thuc">
              <el-date-picker
                v-model="form.ngay_ket_thuc"
                type="date"
                placeholder="Chn ngy ( trng nu vn lm)"
                format="DD/MM/YYYY"
                value-format="YYYY-MM-DD"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
        </el-row>

        <el-form-item label="M t cng vic" prop="mo_ta">
          <el-input
            v-model="form.mo_ta"
            type="textarea"
            :rows="4"
            placeholder="M t chi tit v cng vic, trch nhim, thnh tch..."
          />
        </el-form-item>
      </el-form>

      <template #footer>
        <el-button @click="dialogVisible = false">Hy</el-button>
        <el-button type="primary" :loading="saving" @click="handleSave">
          {{ editIndex === -1 ? 'Thm' : 'Cp nht' }}
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import {ref, reactive, watch} from 'vue';
import {Plus, Edit, Delete} from '@element-plus/icons-vue';
import {ElMessage, ElMessageBox, FormInstance, FormRules} from 'element-plus';
import nhanVienService from '@/services/nhanVienService';
import {NhanVien} from '@/types';
import dayjs from 'dayjs';

const props = defineProps<{
  employee: NhanVien | null;
  loading: boolean;
}>();

const emit = defineEmits<{
  (e: 'reload'): void;
}>();

const formRef = ref<FormInstance>();
const isEditing = ref(false);
const saving = ref(false);
const dialogVisible = ref(false);
const editIndex = ref(-1);

const experiences = ref<any[]>([]);

const form = reactive({
  cong_ty: '',
  chuc_danh: '',
  ngay_bat_dau: '',
  ngay_ket_thuc: '',
  mo_ta: '',
});

const formRules: FormRules = {
  cong_ty: [
    {required: true, message: 'Vui lng nhp tn cng ty', trigger: 'blur'},
  ],
  chuc_danh: [
    {required: true, message: 'Vui lng nhp chc danh', trigger: 'blur'},
  ],
  ngay_bat_dau: [
    {required: true, message: 'Vui lng chn ngy bt u', trigger: 'change'},
  ],
};

watch(
  () => props.employee,
  (newEmployee) => {
    if (newEmployee?.kinh_nghiem_lam_viec) {
      experiences.value = [...newEmployee.kinh_nghiem_lam_viec];
    } else {
      experiences.value = [];
    }
  },
  {immediate: true, deep: true},
);

const formatDate = (date: string | Date | undefined) => {
  if (!date) return 'Hin ti';
  return dayjs(date).format('DD/MM/YYYY');
};

const handleAdd = () => {
  editIndex.value = -1;
  form.cong_ty = '';
  form.chuc_danh = '';
  form.ngay_bat_dau = '';
  form.ngay_ket_thuc = '';
  form.mo_ta = '';
  dialogVisible.value = true;
};

const handleEdit = (index: number) => {
  editIndex.value = index;
  const exp = experiences.value[index];
  form.cong_ty = exp.cong_ty || '';
  form.chuc_danh = exp.chuc_danh || '';
  form.ngay_bat_dau = exp.ngay_bat_dau
    ? dayjs(exp.ngay_bat_dau).format('YYYY-MM-DD')
    : '';
  form.ngay_ket_thuc = exp.ngay_ket_thuc
    ? dayjs(exp.ngay_ket_thuc).format('YYYY-MM-DD')
    : '';
  form.mo_ta = exp.mo_ta || '';
  dialogVisible.value = true;
};

const handleDelete = async (index: number) => {
  try {
    await ElMessageBox.confirm(
      'Bn c chc chn mun xa kinh nghim lm vic ny?',
      'Xc nhn xa',
      {
        confirmButtonText: 'Xa',
        cancelButtonText: 'Hy',
        type: 'warning',
      },
    );

    const updatedExperiences = [...experiences.value];
    updatedExperiences.splice(index, 1);

    saving.value = true;
    await nhanVienService.update(props.employee!._id, {
      kinh_nghiem_lam_viec: updatedExperiences,
    });
    ElMessage.success('Xa kinh nghim lm vic thnh cng');
    emit('reload');
  } catch (err: any) {
    if (err !== 'cancel') {
      ElMessage.error(err.response?.data?.msg || 'Khng th xa kinh nghim');
    }
  } finally {
    saving.value = false;
  }
};

const handleSave = async () => {
  if (!formRef.value) return;
  if (!props.employee) return;

  try {
    const valid = await formRef.value.validate();
    if (!valid) return;

    saving.value = true;

    const updatedExperiences = [...experiences.value];

    if (editIndex.value === -1) {
      // Add new
      updatedExperiences.push({...form});
    } else {
      // Update existing
      updatedExperiences[editIndex.value] = {...form};
    }

    await nhanVienService.update(props.employee._id, {
      kinh_nghiem_lam_viec: updatedExperiences,
    });

    ElMessage.success(
      editIndex.value === -1
        ? 'Thm kinh nghim thnh cng'
        : 'Cp nht kinh nghim thnh cng',
    );
    dialogVisible.value = false;
    emit('reload');
  } catch (err: any) {
    ElMessage.error(err.response?.data?.msg || 'Khng th lu thng tin');
  } finally {
    saving.value = false;
  }
};
</script>

<style lang="scss" scoped>
@import './employee-form-styles.scss';

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.orangehrm-table {
  margin-top: 20px;
}
</style>
