<template>
  <div v-loading="loading" class="orangehrm-memberships">
    <div class="section-header">
      <h3 class="orangehrm-section-title">Thnh vin t chc/Hi</h3>
      <el-button type="primary" :icon="Plus" size="small" @click="handleAdd">
        Thm thnh vin
      </el-button>
    </div>

    <el-table
      v-if="memberships.length > 0"
      :data="memberships"
      border
      class="orangehrm-table"
    >
      <el-table-column prop="to_chuc" label="T chc/Hi" min-width="200" />
      <el-table-column
        prop="loai_thanh_vien"
        label="Loi thnh vin"
        width="150"
      >
        <template #default="scope">
          <el-tag>{{ scope.row.loai_thanh_vien }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="ma_thanh_vien" label="M thnh vin" width="130" />
      <el-table-column label="Thi gian" min-width="180">
        <template #default="scope">
          {{ formatDate(scope.row.ngay_bat_dau) }} -
          {{
            scope.row.ngay_ket_thuc
              ? formatDate(scope.row.ngay_ket_thuc)
              : 'Hin ti'
          }}
        </template>
      </el-table-column>
      <el-table-column label="Ph thnh vin" width="150" align="right">
        <template #default="scope">
          {{
            formatCurrency(scope.row.phi_thanh_vien, scope.row.don_vi_tien_te)
          }}
        </template>
      </el-table-column>
      <el-table-column label="Thao tc" width="150" align="center">
        <template #default="scope">
          <el-button
            size="small"
            :icon="Edit"
            @click="handleEdit(scope.$index)"
          >
            Sa
          </el-button>
          <el-button
            size="small"
            type="danger"
            :icon="Delete"
            @click="handleDelete(scope.$index)"
          >
            Xa
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-empty
      v-else
      description="Cha c thng tin thnh vin t chc"
      :image-size="100"
    />

    <!-- Dialog -->
    <el-dialog
      v-model="dialogVisible"
      :title="editIndex === -1 ? 'Thm thnh vin' : 'Chnh sa thnh vin'"
      width="650px"
    >
      <el-form
        ref="formRef"
        :model="form"
        :rules="formRules"
        label-width="150px"
      >
        <el-form-item label="T chc/Hi" prop="to_chuc" required>
          <el-input
            v-model="form.to_chuc"
            placeholder="Tn t chc, hi, hip hi"
          />
        </el-form-item>

        <el-form-item label="Loi thnh vin" prop="loai_thanh_vien" required>
          <el-select
            v-model="form.loai_thanh_vien"
            placeholder="Chn loi thnh vin"
            style="width: 100%"
          >
            <el-option label="Thnh vin" value="Thnh vin" />
            <el-option label="Hi vin" value="Hi vin" />
            <el-option label="Cng tc vin" value="Cng tc vin" />
            <el-option label="Thnh vin danh d" value="Thnh vin danh d" />
            <el-option label="Khc" value="Khc" />
          </el-select>
        </el-form-item>

        <el-form-item label="M thnh vin" prop="ma_thanh_vien">
          <el-input
            v-model="form.ma_thanh_vien"
            placeholder="M s thnh vin"
          />
        </el-form-item>

        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="Ngy bt u" prop="ngay_bat_dau">
              <el-date-picker
                v-model="form.ngay_bat_dau"
                type="date"
                placeholder="Ngy gia nhp"
                format="DD/MM/YYYY"
                value-format="YYYY-MM-DD"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="Ngy kt thc" prop="ngay_ket_thuc">
              <el-date-picker
                v-model="form.ngay_ket_thuc"
                type="date"
                placeholder="Ngy kt thc"
                format="DD/MM/YYYY"
                value-format="YYYY-MM-DD"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="12">
            <el-form-item label="Ph thnh vin" prop="phi_thanh_vien">
              <el-input-number
                v-model="form.phi_thanh_vien"
                :min="0"
                :precision="0"
                style="width: 100%"
              />
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="n v tin t" prop="don_vi_tien_te">
              <el-select
                v-model="form.don_vi_tien_te"
                placeholder="Chn loi tin"
                style="width: 100%"
              >
                <el-option label="VND" value="VND" />
                <el-option label="USD" value="USD" />
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>

      <template #footer>
        <el-button @click="dialogVisible = false">Hy</el-button>
        <el-button type="primary" :loading="saving" @click="handleSave">
          {{ editIndex === -1 ? 'Thm' : 'Cp nht' }}
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup lang="ts">
import {ref, reactive, watch} from 'vue';
import {Plus, Edit, Delete} from '@element-plus/icons-vue';
import {ElMessage, ElMessageBox, FormInstance, FormRules} from 'element-plus';
import nhanVienService from '@/services/nhanVienService';
import {NhanVien} from '@/types';
import dayjs from 'dayjs';

const props = defineProps<{
  employee: NhanVien | null;
  loading: boolean;
}>();

const emit = defineEmits<{
  (e: 'reload'): void;
}>();

const dialogVisible = ref(false);
const formRef = ref<FormInstance>();
const editIndex = ref(-1);
const saving = ref(false);
const memberships = ref<any[]>([]);

const form = reactive({
  to_chuc: '',
  loai_thanh_vien: '',
  ma_thanh_vien: '',
  ngay_bat_dau: '',
  ngay_ket_thuc: '',
  phi_thanh_vien: 0,
  don_vi_tien_te: 'VND',
});

const formRules: FormRules = {
  to_chuc: [
    {required: true, message: 'Vui lng nhp tn t chc', trigger: 'blur'},
  ],
  loai_thanh_vien: [
    {
      required: true,
      message: 'Vui lng chn loi thnh vin',
      trigger: 'change',
    },
  ],
};

watch(
  () => props.employee,
  (newEmployee) => {
    if (newEmployee) {
      memberships.value = [...(newEmployee.thanh_vien_to_chuc || [])];
    } else {
      memberships.value = [];
    }
  },
  {immediate: true, deep: true},
);

const handleAdd = () => {
  editIndex.value = -1;
  form.to_chuc = '';
  form.loai_thanh_vien = '';
  form.ma_thanh_vien = '';
  form.ngay_bat_dau = '';
  form.ngay_ket_thuc = '';
  form.phi_thanh_vien = 0;
  form.don_vi_tien_te = 'VND';
  dialogVisible.value = true;
};

const handleEdit = (index: number) => {
  editIndex.value = index;
  const membership = memberships.value[index];
  form.to_chuc = membership.to_chuc || '';
  form.loai_thanh_vien = membership.loai_thanh_vien || '';
  form.ma_thanh_vien = membership.ma_thanh_vien || '';
  form.ngay_bat_dau = membership.ngay_bat_dau || '';
  form.ngay_ket_thuc = membership.ngay_ket_thuc || '';
  form.phi_thanh_vien = Number(
    membership.phi_thanh_vien?.$numberDecimal || membership.phi_thanh_vien || 0,
  );
  form.don_vi_tien_te = membership.don_vi_tien_te || 'VND';
  dialogVisible.value = true;
};

const handleDelete = async (index: number) => {
  try {
    await ElMessageBox.confirm(
      'Bn c chc chn mun xa thng tin thnh vin ny?',
      'Xc nhn xa',
      {
        confirmButtonText: 'Xa',
        cancelButtonText: 'Hy',
        type: 'warning',
      },
    );

    const updatedMemberships = [...memberships.value];
    updatedMemberships.splice(index, 1);

    saving.value = true;
    await nhanVienService.update(props.employee!._id, {
      thanh_vien_to_chuc: updatedMemberships,
    });
    ElMessage.success('Xa thnh vin t chc thnh cng');
    emit('reload');
  } catch (err: any) {
    if (err !== 'cancel') {
      ElMessage.error(
        err.response?.data?.msg || 'Khng th xa thnh vin t chc',
      );
    }
  } finally {
    saving.value = false;
  }
};

const handleSave = async () => {
  if (!formRef.value) return;
  if (!props.employee) return;

  try {
    const valid = await formRef.value.validate();
    if (!valid) return;

    saving.value = true;

    const updatedMemberships = [...memberships.value];

    if (editIndex.value === -1) {
      updatedMemberships.push({...form});
    } else {
      updatedMemberships[editIndex.value] = {...form};
    }

    await nhanVienService.update(props.employee._id, {
      thanh_vien_to_chuc: updatedMemberships,
    });

    ElMessage.success(
      editIndex.value === -1
        ? 'Thm thnh vin t chc thnh cng'
        : 'Cp nht thnh vin t chc thnh cng',
    );
    dialogVisible.value = false;
    emit('reload');
  } catch (err: any) {
    ElMessage.error(
      err.response?.data?.msg || 'Khng th lu thnh vin t chc',
    );
  } finally {
    saving.value = false;
  }
};

const formatDate = (date: any) => {
  if (!date) return '';
  return dayjs(date).format('DD/MM/YYYY');
};

const formatCurrency = (amount: any, currency: string) => {
  const num = Number(amount?.$numberDecimal || amount || 0);
  if (currency === 'VND') {
    return new Intl.NumberFormat('vi-VN', {
      style: 'currency',
      currency: 'VND',
    }).format(num);
  } else {
    return new Intl.NumberFormat('en-US', {
      style: 'currency',
      currency: 'USD',
    }).format(num);
  }
};
</script>

<style lang="scss" scoped>
@import './employee-form-styles.scss';

.section-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.orangehrm-section-title {
  font-size: $font-size-lg;
  font-weight: $font-weight-bold;
  color: $text-primary;
  margin: 0;
}

.orangehrm-table {
  margin-top: 20px;
}
</style>
